.anima-underline {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-perspective: 1000;
  perspective: 1000;
  text-decoration: none;
  color: #ECE9E6;
  transition: color 0.4s cubic-bezier(0.23, 1, 0.32, 1);
  position: relative;
}

.anima-underline::before {
  content: "";
  display: block;
  position: absolute;
  right: .0625rem;
  bottom: -.125rem;
  left: .0625rem;
  height: .1rem;
  background-color: #ECE9E6;
  transform: scaleX(0);
  transform-origin: 100% 50%;
  transition: transform 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}

.anima-underline:hover, .anima-underline.active {
  color: #ECE9E6;
}

.anima-underline:hover::before, .anima-underline.active::before {
  transform: scaleX(1);
  transform-origin: 0 50%;
}

